import React, { Component, Fragment } from 'react';
import './App.css';

class App extends Component {
  state = { usedCards: [], question: 0, right: 0, wrong: 0 };

  componentDidMount() {
    const vh = window.innerHeight * 0.01;

    document.documentElement.style.setProperty('--vh', `${vh}px`);
  }

  getRandomCard = () => {
    const { usedCards } = this.state;
    const rando = Math.floor(Math.random() * 200) + 1;

    let card;
    let newCards = usedCards;
  
    if (rando % 2) card = rando;
    else card = rando + 1;
  
    if (usedCards.includes(card)) {
      this.getRandomCard();
    } else {
      newCards.push(card);

      this.setState({ usedCards: newCards });
    }
  }

  getCardSequence = count => {
    this.setState({ usedCards: [], question: 0, right: 0, wrong: 0 }, () => {
      for (let i = 0; i < count; i++) {
        this.getRandomCard();
      }
    });
  }

  handleRightAnswer = () => {
    const { question, right } = this.state;
    const nextQuestion = question + 1;
    const updatedRight = right + 1;

    this.setState({ answerVisible: false, question: nextQuestion, right: updatedRight });
  }

  handleWrongAnswer = () => {
    const { question, wrong } = this.state;
    const nextQuestion = question + 1;
    const updatedWrong = wrong + 1;

    this.setState({ answerVisible: false, question: nextQuestion, wrong: updatedWrong });
  }

  handleShowAnswer = () => {
    this.setState({ answerVisible: true });
  }

  render() {
    const { answerVisible, usedCards, question, right } = this.state;
    const { getCardSequence, handleShowAnswer, handleRightAnswer, handleWrongAnswer } = this;

    return (
      <div className="App">
        {usedCards.length === 0 || question === usedCards.length ? 
          <Fragment>
            <div className="button" onClick={() => getCardSequence(10)}>Test ten cards</div>
            <div className="button" onClick={() => getCardSequence(100)}>Test all cards</div>

            <br />
            <br />

            Last attempt: {`${right}/${question}`}
          </Fragment>
          : null
        }
  
        {usedCards.length && question !== usedCards.length ? <Fragment>
          <object id="question" type="application/pdf" data={`http://bradevans.us/citizenship/cards/0${usedCards[question]}.pdf#toolbar=0&navpanes=0&scrollbar=0`} width="100%">
          
          </object>
  
          <br />
  
          {!answerVisible && <div className="show-answer" onClick={handleShowAnswer}>
            Show Answer
          </div>}

          {answerVisible && <object id="answer" type="application/pdf" data={`http://bradevans.us/citizenship/cards/0${usedCards[question] + 1}.pdf#toolbar=0&navpanes=0&scrollbar=0`} width="100%">
            
          </object>}
          
          <div className="answered">
            <div onClick={handleWrongAnswer}>Wrong</div>
            <div onClick={handleRightAnswer}>Right</div>
          </div>

          <br />
          <br />

          {`${right}/${question}`}
  
          </Fragment>
          : null
        }
  
      </div>
    );
  }
  
}

export default App;
